import {PanelPlugin} from '@grafana/data';
import {SimplePanel} from './SimplePanel';
import {GeojsonMapOptions} from './types';

export const plugin = new PanelPlugin<GeojsonMapOptions>(SimplePanel).setPanelOptions(builder => {
    return builder

        .addSelect({
            path: 'map.tileLayer',
            name: 'Map - Base Layer',
            defaultValue: 'ch.swisstopo.pixelkarte-farbe',
            settings: {
                options: [
                    {
                        value: 'ch.swisstopo.pixelkarte-farbe',
                        label: 'ch.swisstopo.pixelkarte-farbe',
                    },
                    {
                        value: 'ch.swisstopo.pixelkarte-grau',
                        label: 'ch.swisstopo.pixelkarte-grau',
                    },
                    {
                        value: 'ch.swisstopo.pixelkarte-farbe-winter',
                        label: 'ch.swisstopo.pixelkarte-farbe-winter',
                    },
                    {
                        value: 'ch.swisstopo.pixelkarte-farbe-pk25.noscale',
                        label: 'ch.swisstopo.pixelkarte-farbe-pk25.noscale',
                    },
                    {
                        value: 'ch.swisstopo.pixelkarte-farbe-pk50.noscale',
                        label: 'ch.swisstopo.pixelkarte-farbe-pk50.noscale',
                    },
                    {
                        value: 'ch.swisstopo.pixelkarte-farbe-pk100.noscale',
                        label: 'ch.swisstopo.pixelkarte-farbe-pk100.noscale',
                    },
                    {
                        value: 'ch.swisstopo.pixelkarte-farbe-pk200.noscale',
                        label: 'ch.swisstopo.pixelkarte-farbe-pk200.noscale',
                    },
                    {
                        value: 'ch.swisstopo.pixelkarte-farbe-pk500.noscale',
                        label: 'ch.swisstopo.pixelkarte-farbe-pk500.noscale',
                    },
                    {
                        value: 'ch.swisstopo.pixelkarte-farbe-pk1000.noscale',
                        label: 'ch.swisstopo.pixelkarte-farbe-pk1000.noscale',
                    }
                ],
            }
        })
        .addNumberInput({
            path: 'map.zoom',
            name: 'Map - Initial zoom',
            defaultValue: 16,
        })
        .addTextInput({
            path: 'map.tileAttribution',
            name: 'Map - Attribution Note',
            defaultValue: '&copy; <a href="https://www.swisstopo.ch/" target="_blank">Swisstopo</a>',
        })
        .addNumberInput({
            path: 'map.centerLatitude',
            name: 'Map - Initial Center latitude',
            defaultValue: 2600000,  //Bern
        })
        .addNumberInput({
            path: 'map.centerLongitude',
            name: 'Map - Initial Center longitude',
            defaultValue: 1200000, //Bern
        })
        .addBooleanSwitch({
            path: 'map.useBoundsInQuery',
            name: 'Map - Map bounds as variables',
            defaultValue: false,
            showIf: () => false //TODO : use bounds in Plugin
        })
        .addSelect({
            path: 'map.type',
            name: 'Map - Type',
            defaultValue: 'gemeinde',
            settings: {
                options: [
                    {
                        value: 'gemeinde',
                        label: 'Gemeinde',
                    },
                    {
                        value: 'bezirk',
                        label: 'Bezirk',
                    }
                ]
            }
        })
        //Gemeinde
        //==========================================================================================
        //==========================================================================================
        .addTextInput({
            path: "data.gemeinde.identifier_field",
            name: 'Gemeinde - Identifier Field',
            defaultValue: "bfs_nummer_gemeinde",
            showIf: currentOptions => currentOptions.map.type == 'gemeinde'
        })
        .addTextInput({
            path: "data.gemeinde.value_field",
            name: 'Gemeinde - Value Field',
            defaultValue: "anzahl_gemeinde",
            showIf: currentOptions => currentOptions.map.type == 'gemeinde'
        })
        //Gemeinde - matched Data
        //##########################################################################################
        .addBooleanSwitch({
            path: "data.gemeinde.matchedData.showTooltip",
            name: 'Gemeinde (matched Data) - Show Tooltip',
            defaultValue: true,
            showIf: currentOptions => currentOptions.map.type == 'gemeinde'
        })
        .addTextInput({
            path: "data.gemeinde.matchedData.tooltipText",
            name: 'Gemeinde (matched Data) - Tooltip Text',
            defaultValue: "Gemeinde (matched Data) - lorem impsum dolor",
            showIf: currentOptions => currentOptions.map.type == 'gemeinde' && currentOptions.data.gemeinde.matchedData.showTooltip
        })
        .addNumberInput({
            path: 'data.gemeinde.customMinValue',
            name: 'Gemeinde (matched Data) - Custom minimum value (Leave Empty to be calculated)',
            defaultValue: undefined,
            showIf: currentOptions => currentOptions.map.type == 'gemeinde'
        })
        .addTextInput({
            path: 'data.gemeinde.matchedData.style.minFillColor',
            name: 'Gemeinde (matched Data) - Fill Color for minimum value (Hex)',
            defaultValue: '#c3d9ef',
            showIf: currentOptions => currentOptions.map.type == 'gemeinde'
        })
        .addNumberInput({
            path: 'data.gemeinde.customMaxValue',
            name: 'Gemeinde (matched Data) - Custom maximum value (Leave Empty to be calculated)',
            defaultValue: undefined,
            showIf: currentOptions => currentOptions.map.type == 'gemeinde'
        })
        .addTextInput({
            path: 'data.gemeinde.matchedData.style.maxFillColor',
            name: 'Gemeinde (matched Data) - Fill Color for maximum value (Hex)',
            defaultValue: '#112e4a',
            showIf: currentOptions => currentOptions.map.type == 'gemeinde'
        })
        .addTextInput({
            path: 'data.gemeinde.matchedData.style.strokeColor',
            name: 'Gemeinde (matched Data) - Border color (Hex)',
            defaultValue: '#000000',
            showIf: currentOptions => currentOptions.map.type == 'gemeinde'
        })
        .addNumberInput({
            path: 'data.gemeinde.matchedData.style.strokeWidth',
            name: 'Gemeinde (matched Data) - Border width',
            defaultValue: 1.0,
            showIf: currentOptions => currentOptions.map.type == 'gemeinde'
        })
        .addNumberInput({
            path: 'data.gemeinde.matchedData.style.fillOpacity',
            name: 'Gemeinde (matched Data) - Fill opacity',
            defaultValue: 1.0,
            showIf: currentOptions => currentOptions.map.type == 'gemeinde'
        })
        //Gemeinde - NON matched Data
        //##########################################################################################
        .addBooleanSwitch({
            path: "data.gemeinde.nonMatchedData.showTooltip",
            name: 'Gemeinde (NON matched Data) - Show Tooltip',
            defaultValue: true,
            showIf: currentOptions => currentOptions.map.type == 'gemeinde'
        })
        .addTextInput({
            path: "data.gemeinde.nonMatchedData.tooltipText",
            name: 'Gemeinde (NON matched Data) - Tooltip Text',
            defaultValue: "Keine Daten fr die Gemeinde {{name}}",
            showIf: currentOptions => currentOptions.map.type == 'gemeinde' && currentOptions.data.gemeinde.nonMatchedData.showTooltip
        })
        .addTextInput({
            path: 'data.gemeinde.nonMatchedData.style.fillColor',
            name: 'Gemeinde (NON matched Data) - Fill Color (Hex)',
            defaultValue: '#E5E5E5',
            showIf: currentOptions => currentOptions.map.type == 'gemeinde'
        })
        .addTextInput({
            path: 'data.gemeinde.nonMatchedData.style.strokeColor',
            name: 'Gemeinde (NON matched Data) - Border color (Hex)',
            defaultValue: '#000000',
            showIf: currentOptions => currentOptions.map.type == 'gemeinde'
        })
        .addNumberInput({
            path: 'data.gemeinde.nonMatchedData.style.strokeWidth',
            name: 'Gemeinde (NON matched Data) - Border width',
            defaultValue: 1.0,
            showIf: currentOptions => currentOptions.map.type == 'gemeinde'
        })
        .addNumberInput({
            path: 'data.gemeinde.nonMatchedData.style.fillOpacity',
            name: 'Gemeinde (NON matched Data) - Fill opacity',
            defaultValue: 1.0,
            showIf: currentOptions => currentOptions.map.type == 'gemeinde'
        })
        //Bezirk
        //==========================================================================================
        //==========================================================================================
        .addTextInput({
            path: "data.bezirk.identifier_field",
            name: 'Bezirk - Identifier Field',
            defaultValue: "bfs_nummer_bezirk",
            showIf: currentOptions => currentOptions.map.type == 'bezirk'
        })
        .addTextInput({
            path: "data.bezirk.value_field",
            name: 'Bezirk - Value Field',
            defaultValue: "anzahl_bezirk",
            showIf: currentOptions => currentOptions.map.type == 'bezirk'
        })
        //Bezirk - matched Data
        //##########################################################################################
        .addBooleanSwitch({
            path: "data.bezirk.matchedData.showTooltip",
            name: 'Bezirk (matched Data) - Show Tooltip',
            defaultValue: true,
            showIf: currentOptions => currentOptions.map.type == 'bezirk'
        })
        .addTextInput({
            path: "data.bezirk.matchedData.tooltipText",
            name: 'Bezirk (matched Data) - Tooltip Text',
            defaultValue: "Bezirk (matched Data) - lorem impsum dolor",
            showIf: currentOptions => currentOptions.map.type == 'bezirk' && currentOptions.data.bezirk.matchedData.showTooltip
        })
        .addNumberInput({
            path: 'data.bezirk.customMinValue',
            name: 'Bezirk (matched Data) - Custom minimum value (Leave Empty to be calculated)',
            defaultValue: undefined,
            showIf: currentOptions => currentOptions.map.type == 'bezirk'
        })
        .addTextInput({
            path: 'data.bezirk.matchedData.style.minFillColor',
            name: 'Bezirk (matched Data) - Fill Color for minimum value (Hex)',
            defaultValue: '#c3d9ef',
            showIf: currentOptions => currentOptions.map.type == 'bezirk'
        })
        .addNumberInput({
            path: 'data.bezirk.customMaxValue',
            name: 'Bezirk (matched Data) - Custom maximum value (Leave Empty to be calculated)',
            defaultValue: undefined,
            showIf: currentOptions => currentOptions.map.type == 'bezirk'
        })
        .addTextInput({
            path: 'data.bezirk.matchedData.style.maxFillColor',
            name: 'Bezirk (matched Data) - Fill Color for maximum value (Hex)',
            defaultValue: '#112e4a',
            showIf: currentOptions => currentOptions.map.type == 'bezirk'
        })
        .addTextInput({
            path: 'data.bezirk.matchedData.style.strokeColor',
            name: 'Bezirk (matched Data) - Border color (Hex)',
            defaultValue: '#000000',
            showIf: currentOptions => currentOptions.map.type == 'bezirk'
        })
        .addNumberInput({
            path: 'data.bezirk.matchedData.style.strokeWidth',
            name: 'Bezirk (matched Data) - Border width',
            defaultValue: 1.0,
            showIf: currentOptions => currentOptions.map.type == 'bezirk'
        })
        .addNumberInput({
            path: 'data.bezirk.matchedData.style.fillOpacity',
            name: 'Bezirk (matched Data) - Fill opacity',
            defaultValue: 1.0,
            showIf: currentOptions => currentOptions.map.type == 'bezirk'
        })
        //Bezirk - NON matched Data
        //##########################################################################################
        .addBooleanSwitch({
            path: "data.bezirk.nonMatchedData.showTooltip",
            name: 'Bezirk (NON matched Data) - Show Tooltip',
            defaultValue: true,
            showIf: currentOptions => currentOptions.map.type == 'bezirk'
        })
        .addTextInput({
            path: "data.bezirk.nonMatchedData.tooltipText",
            name: 'Bezirk (NON matched Data) - Tooltip Text',
            defaultValue: "Keine Daten fr den Bezirk {{name}}",
            showIf: currentOptions => currentOptions.map.type == 'bezirk' && currentOptions.data.bezirk.nonMatchedData.showTooltip
        })
        .addTextInput({
            path: 'data.bezirk.nonMatchedData.style.fillColor',
            name: 'Bezirk (NON matched Data) - Fill Color (Hex)',
            defaultValue: '#E5E5E5',
            showIf: currentOptions => currentOptions.map.type == 'bezirk'
        })
        .addTextInput({
            path: 'data.bezirk.nonMatchedData.style.strokeColor',
            name: 'Bezirk (NON matched Data) - Border color (Hex)',
            defaultValue: '#000000',
            showIf: currentOptions => currentOptions.map.type == 'bezirk'
        })
        .addNumberInput({
            path: 'data.bezirk.nonMatchedData.style.strokeWidth',
            name: 'Bezirk (NON matched Data) - Border width',
            defaultValue: 1.0,
            showIf: currentOptions => currentOptions.map.type == 'bezirk'
        })
        .addNumberInput({
            path: 'data.bezirk.nonMatchedData.style.fillOpacity',
            name: 'Bezirk (NON matched Data) - Fill opacity',
            defaultValue: 1.0,
            showIf: currentOptions => currentOptions.map.type == 'bezirk'
        });
});
